.pagination {
    justify-content: center;
    list-style-type: none;
    display: flex;
    gap: 5px;
    flex-wrap: wrap;
    padding: 6px;
    border-radius: 50px;
    flex-grow: 1;
    margin-top: 5px;
}

.block-pagin {
    justify-content: center;
}

.pagins {
    text-decoration: none;
    display: inline-block;
    color: white;
    padding: 10px 10px;
    margin: 20px 1px;
    border-radius: 5px;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    background-image: linear-gradient(to right, #9EEFE1 0%, #4830F0 51%, #9EEFE1 100%);
    background-size: 200% auto;
    box-shadow: 0 0 20px rgba(0, 0, 0, .1);
    transition: .5s;
    }
.pagins:hover {
    background-position: right center;
}

.pagins-l {
    text-decoration: none;
    display: inline-block;
    color: white;
    padding: 3px 3px;
    margin: 15px 10px;
    border-radius: 10px;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    background-image: linear-gradient(to right, #9EEFE1 0%, #4830F0 51%, #9EEFE1 100%);
    background-size: 200% auto;
    box-shadow: 0 0 20px rgba(0, 0, 0, .1);
    transition: .5s;
    
}

.pagins-l:hover {
    background-position: right center;
}

.pagins-r {
    text-decoration: none;
    display: inline-block;
    color: white;
    padding: 3px 3px;
    margin: 15px 10px;
    border-radius: 10px;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    background-image: linear-gradient(to right, #9EEFE1 0%, #4830F0 51%, #9EEFE1 100%);
    background-size: 200% auto;
    box-shadow: 0 0 20px rgba(0, 0, 0, .1);
    transition: .5s;
}

.pagins-r:hover {
    background-position: center right ;
}

button {
    text-align: center;
    text-decoration: none;
    display: inline-block;
    color: white;
    padding: 5px 5px;
    margin: 5px 5px;
    border-radius: 5px;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    letter-spacing: 2px;
    background-image: linear-gradient(to right, #9EEFE1 0%, #4830F0 51%, #9EEFE1 100%);
    background-size: 200% auto;
    box-shadow: 0 0 20px rgba(0, 0, 0, .1);
    transition: .5s;
    }



button:hover {
    background-position: right center;
}

